*{margin: 0; padding: 0;}
a{text-decoration: none;}
ul,li,ol{list-style: none;}
body{
  background: #f1f1f1;
}
button{
  border: none;
  background: none;
}

/* nav 부분 */
.navbar-expand-lg{
  background: #fff;
} 

/* .navbarScroll nav{
 
} */

.container-fluid{
  width: 64.5%; 
  display: flex;
  justify-content: space-between;
}

.navbar-brand{
  width: 8%;
}

.navbar-brand img{
  width: 100%;
}

.nav-btn{
  border:none;
  color: rgb(102, 102, 102);
}

.nav-search{
  width: 100%;
  position: absolute;
  top: 0;
  left: 50%;
  transform: translate(-50%,0%);
  z-index: 5;
  background: #fff;
  padding: 2% 1% 0 1%;
  font-size: 0.8vw;
}

.nav-search-top{
  width: 64%;
  margin: 0 auto;
  display: flex;
  justify-content: space-between;
  color: #777;
}

.nav-search-top button{
  font-size: 1.2vw;
  padding-right: 1%;
}

.nav-search-middle{
  width: 64%;
  margin: 0 auto 2% auto;
  padding: 1% 0;
  border-bottom: 1px solid #eee;
}

.nav-search-middle input{
  width: 100%;
  border: none;
  position: relative;
  font-size: 1.5em;
  color: black;
  padding: 1% 0.5% 0.5%;
}

.nav-search-middle button{
  position: absolute;
  right:19%;
  font-size: 1.5em;
  padding: 0.5% 0.5% 0;
}

.search-recommend{
  display: flex;
  justify-content: space-between;
  align-content: center;
  width: 64%;
  margin: 0 auto;
}

.search-recommend div{
  width: 15%;
}

.search-recommend>div>p{
  text-align: center;
}

.search-recommend div div{
  width: 100%;
  height: 70%;
  padding-bottom: 10%;
  box-shadow: 2px 2px 5px #eee;
}

.search-recommend div div img{
  width: 100%;
  height: 100%;
}

.search-recommend div p{
  padding: 2%;
  line-height: 4vw;
}

.search-recommend div p:hover{
  color: gray;
}

.nav-search ul{
  width: 68%;
  margin: 0 auto;
  padding-bottom: 2%;
}

/* text 부분 */
.text1{
  text-align: center;
  font-size: 1.4vw;
}

/* main img 부분 */
.main{
  width: 100%;
  display: flex;
}

.main-left{
  width: 50%;
  /* padding:  */
  display: flex;
  justify-content: space-evenly;
}

.main-left-first, .main-left-second{
  width: 45%;
}

.main-right{
  width: 50%;
  padding-right: 1%;
}

.main-right>.col h4{
  padding: 2% 0;
}


.col h4{
  padding: 4% 0;
  text-align: center;
  font-weight: 400;
  font-size: 1.2vw;
  cursor: pointer;
}

.col h4:hover{
  color: gray;
}

.col p{
  text-align: center;
}

/* read 부분 */
.read{
  width: 100%;
}

.up-down{
  width: 63.5%;
  margin: 0 auto;
}

.up-down p{
  width: 43.2%;
  font-size: 1.5vw;
  font-weight: 350;
  padding: 0 1%;
}

.up,.down{
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-top: 10%;
  
}

.up img, .down img{
  padding: 0 1%;
}

/* footer 부분*/
.bottom{
  width: 100%;
  background: #303030;
  color: #888;
}

.Footer{
  display: flex;
  justify-content: space-between;
  width: 66%;
  margin: 5% auto 0;
  padding: 3vw 0 1.5vw 0;
}


.Footer>ul>li a{
  color: #888;
}

.Footer>ul>li:first-child{
  padding-bottom: 18%;
}

.Footer>ul>li:first-child a{
  color: #fff;
}

.footer-one{
  list-style: none;
}

.footer-one li, .footer-two li{
  padding-bottom: 0.4vw;
}

.footer-three h6{
  color: #fff;
  font-weight: 400;
}

.footer-three{
  width: 50%;
  height: 11vw;
  padding-right: 3%;
  display: flex;
  flex-wrap: wrap;
  align-content: space-between;
}

.search{
  width: 100%;}

.search input{
  width: 82%;
  height: 2.2vw;
  color: #888;
  padding: 1.3%;
  border: none;
  position: relative;
}

.search button{
  width: 5.5%;
  height: 2.2vw;
  background: #888;
  color: #fff;
  padding: 0.4%;
  border-radius: 0 5px 5px 0;
  position: absolute;
}

.footer-icon{
  width: 100%;
}

.footer-icon i{
  padding-right: 4%;
}

.last{
  width: 100%;
  background: #222;
  color: #888;
  padding: 0.6vw 0 0.5vw 0;
}

.last p{
  margin-left: 2.5%;
  line-height: 0.1vw;
}

.fa-regular{
  color: tomato;
}

.last1{
  width: 66%; 
  margin: 0 auto;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
}

.last-bar{
  display: flex;
  justify-content:flex-start;
  align-items: flex-end;
}

.last-bar a{
  width: 10%;
  margin: 0.5% 0 0.5% 3.3%;
}

.last-bar a img{
  width: 100%;
}

.last-bar1{
    margin: 0.5% 3% 0 0;
}

.last-bar1 img{
  width: 100%;

}
